import {
    ApiUrl
} from './url.js';
const ajax = (url,data,method,success) => {
	let baseUrl=ApiUrl.toString();
	if(method==undefined){
		method ='POST';
	}
   
    success = success || function () {};
	let token;
	uni.getStorage({
    key: 'storage_key',
    success: function (res) {
        token=res.data;
       }
    });
    uni.request({
        url: baseUrl + url,
        data: method=='POST'?JSON.stringify(data):data,
        method: method,
        header:token==undefined||null?{"Content-Type": "application/json"}:{"authorization": token},
        dataType: 'json',
        success: function (res) {
            success(res);
        },
        fail: function () {
            uni.showToast({
                title: '请稍后重试'
            });
        }
    })
}

export {
    ajax
}


